@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
@import "../../../styles/variables.scss" ;

.home-menu-main {
    background-color: #1b1919;
    height: 100vh;

    .home-menu {
        display: flex;
        justify-content: space-between;
        padding: 30px 150px;
        font-family: $police;
        background-color: #1b1919;
    
        .home-menu-link {
            text-decoration: none;
            font-size: 20px;
            color: white;
      
            &:hover {
                text-decoration: underline;
            }
        }
    
        .home-menu-logo {
            text-decoration: none;
            color: white;
            font-size: 25px;
            font-weight: semi-bold;
        }
    }
    
    .home-section {
        display: flex;
        justify-content: space-between;
        padding: 0 16%;
        padding-top: 60px;
        background-color: #1b1919;
    
        & img {
            height: 600px;
            width: 400px;
        }
    
        .home-section-div {
            width: 50%;
            display: flex;
            flex-direction: column;
            padding: 150px 50px;
            font-family: $police;
    
            & h1 {
                color: white;
                margin-bottom: 15px;
            }
    
            & p {
                color: white;
                margin-bottom: 40px;
                font-size: 20px;
            }
    
            & button {
                width: 150px;
                height: 45px;
                color: white;
                border: none;
                border-radius: 10px;
                background-color: #10c6d3;
                font-size: 16px;
                font-family: $police;
                font-weight: bold;
                cursor: pointer;
    
                &:hover {
                    transform: scale(1.1) translateX(8px);
                    transition-duration: 1s;
                }
            }
        }
    }
}